/* Font Faces
–––––––––––––––––––––––––––––––––––––––––––––––––– */
@use '../abstracts/tools' as *;

/* lato-300 - latin */
@include font-face("Lato", lato-v23-latin-300, normal, 300);

/* lato-regular - latin */
@include font-face("Lato", lato-v23-latin-regular, normal, 400);

/* lato-900 - latin */
@include font-face("Lato", lato-v23-latin-900, normal, 900);

/* open-sans-300 - latin */
@include font-face("Open Sans", open-sans-v29-latin-300, normal, 300);

/* open-sans-regular - latin */
@include font-face("Open Sans", open-sans-v29-latin-regular, normal, 400);

/* open-sans-700 - latin */
@include font-face("Open Sans", open-sans-v29-latin-700, normal, 700);

/* open-sans-italic - latin */
@include font-face("Open Sans", open-sans-v29-latin-italic, italic, 400);